@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{background-color:#282828;display:inline-flex;align-items:center;justify-content:center;min-width:100vw;min-height:100vh}.fade-in-out{display:inline-flex;justify-content:center;animation:fadeIn linear 3s;-webkit-animation:fadeIn linear 3s;-moz-animation:fadeIn linear 3s;-o-animation:fadeIn linear 3s;-ms-animation:fadeIn linear 3s}.fade-in-out img{width:80%;height:80%}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

