.captcha-wrapper #g-recaptcha{overflow:hidden}@media only screen and (max-width: 667px), screen and (orientation: portrait){.captcha-wrapper #g-recaptcha{transform:scale(0.77);-webkit-transform:scale(0.77);transform-origin:0 0;-webkit-transform-origin:0 0}}

.form__group{position:relative;padding:15px 0 0;margin-top:10px;margin-bottom:10px}.form__field{font-family:inherit;min-width:300px;border:0;border-bottom:2px solid #9b9b9b;outline:0;font-size:1.3rem;color:#fff;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__field:-ms-input-placeholder{color:transparent}.form__field::placeholder{color:transparent}.form__field:-ms-input-placeholder ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__field:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#9b9b9b}.form__field:focus{padding-bottom:6px;font-weight:700;border-width:3px;border-image:linear-gradient(to right, #cdc727, #ff0);border-image-slice:1}.form__field:focus ~ .form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#cdc727;font-weight:700}.form__field:required,.form__field:invalid{box-shadow:none}.wrapper{font-family:'Poppins', sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#282828}.auth-btn-wrapper{margin-top:20px;display:flex;justify-content:center}

